// ========================
// State Stories
export const StateError = {
    render: renderTemplate,
    args: { label: 'Error State', placeholder: 'Enter at least 5 characters', size: 'md', color: 'error', iconStart: 'error' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input) {
            input.value = 'Error State';
            input.dispatchEvent(new Event('input'));
            input.dispatchEvent(new Event('blur'));
        }
    },
};
export const StateFocus = {
    render: renderTemplate,
    args: { label: 'Focus State', placeholder: 'Typing...', size: 'md', color: 'focus', iconStart: 'search' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input)
            input.focus();
    },
};
export const StateDisabled = {
    render: (args) => {
        const control = new FormControl({ value: 'Disabled', disabled: true });
        const templateCode = `
<app-searchfield
  label="${args.label}"
  placeholder="${args.placeholder}"
  [control]="control"
  size="${args.size}"
  color="${args.color}"
  ${args.iconStart ? `iconStart="${args.iconStart}"` : ''}
  ${args.iconEnd ? `iconEnd="${args.iconEnd}"` : ''}>
</app-searchfield>
`;
        return {
            props: { ...args, control },
            template: templateCode,
            parameters: { docs: { source: { code: templateCode } } },
        };
    },
    args: { label: 'Disabled', placeholder: 'Cannot type', size: 'md', color: 'primary', iconStart: 'lock' },
};
// ========================
// Filled State
export const StateFilled = {
    render: renderTemplate,
    args: { label: 'Filled State', placeholder: 'Placeholder here', size: 'md', color: 'success', iconStart: 'check' },
    play: async ({ canvasElement }) => {
        const input = canvasElement.querySelector('input');
        if (input) {
            input.value = 'Hello world';
            input.dispatchEvent(new Event('input'));
        }
    },
};
